import { Component, OnInit } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { CustomValidators } from 'ng2-validation';

import { GetChemistDataService } from '../../services/GetChemistDataService';
import { AddAreaService } from '../../services/AddAreaService';
import { GetPopularChemistService } from '../../services/GetPopularChemistService';
import { AddchemistService } from '../../services/AddChemistService';

import { ValidationModel } from './../../models/validation.class';
import { Location } from '@angular/common';
import { UserService } from '../../services/userService';
import swal from 'sweetalert2';
declare var jQuery: any;

@Component({
  selector: 'add-chemist',
  templateUrl: './add-chemist.component.html',
  styleUrls: [
    './add-chemist.component.scss',
    '../../../../assets/icon/icofont/css/icofont.scss'
  ],
  providers: [UserService,GetChemistDataService, AddAreaService, GetPopularChemistService, AddchemistService]
})
export class AddChemistComponent implements OnInit {
  public FullName: string = "";
  public Address: string = "";
  public Email: string = "";
  public PharmaCommission: number = 0;
  public City: number = 0;
  public ProfilePicture: File;
  public LogoUrl:any;
  public Area: number = 0;
  public PharmacistId: number = 0;
  // public Username: string = "";
  // public Password: string = "";
  public Latitude: number = 0;
  public Longitude: number = 0;

  public cities_array = [];
  public areas_array = [];
  public chemists_array = [];
  public CityName: any;
  public AreaName: any;
  public chemistData = {
    Email: '',
    ProfileURL:'',
    Chemist: {
      CityId:0,
      PharmacyName:'',
      Address: '',
      ContactPersonName: '',
      PharmaCommission: 0,
      City: {
        Id: '',
        Name: ''
      },
      Area: {
        City: {
          Id: '',
          Name: ''
        }

      }
    }
  }
  public runner: boolean = false;


  public pharmacist_id_error:boolean = false;
  public image_error:boolean = false;
  public full_name_id_error:boolean = false;
  public address_id_error:boolean = false;
  public PharmacyName_error:boolean = false;
  public email_id_error:boolean = false;
  public email_invalid_id_error:boolean = false;
  public pharma_commission_id_error:boolean = false;
  public city_id_error:boolean = false;
  public area_id_error:boolean = false;
  public latitude_id_error:boolean = false;
  public longitude_id_error:boolean = false;
  public addLoader:boolean = false;
  public actionBtn:boolean = true;


  constructor(public _getChemistDataService: GetChemistDataService,
    public _getPopularChemistService: GetPopularChemistService,
    public _AddAreaNameService: AddAreaService,
    public _userService:UserService,
    public _addChemistServiceService: AddchemistService) {
    this.getAllCities();
    this.getChemists('');
  }
  buildMap(lat, lng) {
    var self = this;
    jQuery('#somecomponent').locationpicker({
      location: {
        latitude: +lat || 25.0700,
        longitude: +lng || 67.2848
      },
      radius: 300,
      inputBinding: {
        latitudeInput: self.Latitude,
        longitudeInput: self.Longitude,
        radiusInput: jQuery('#us7-radius'),
        locationNameInput: jQuery('#us7-address'),
      },
      onchanged: function (currentLocation, radius, isMarkerDropped) {
        console.log(currentLocation);  //latlon  
        self.Latitude = currentLocation.latitude;
        self.Longitude = currentLocation.longitude;
      },
      enableAutocomplete: true,
      resize: true,
      autosize: true
    });
    self.Latitude = lat;
    self.Longitude = lng;
  }


  changeImage() {
    $("#imgInp").click();
  }

  changeimage(elem) {
    this.readURL(elem);
  }
  readURL(input) {


    var reader: any,
      target: EventTarget;
    reader = new FileReader();

    input = document.getElementById('imgInp');
    if (input.files && input.files[0]) {
      // var reader = new FileReader();
      var self = this;
      reader.onload = function (e) {
        console.log(e);
        $('#blah').attr('src', e.target.result);
        self.ProfilePicture = input.files[0];
        $("#blah").css("width", "50px");
        self.saveImageService();
      }

      reader.readAsDataURL(input.files[0]);

     
    }
  }


  saveImageService(){
   
      this._userService.saveImageForPharmacy(this.ProfilePicture).subscribe(a=>{
        this.LogoUrl = a.data;
      })
  }

  ngOnInit(): void {
    var self = this;
    jQuery('#somecomponent').html("Loading Map");
    navigator.geolocation.getCurrentPosition(showPosition);
    function showPosition(position) {
      var lat = position.coords.latitude;
      var lng = position.coords.longitude;
      $('.map-lat').val(lat);
      $('.map-lon').val(lng);
      self.buildMap(lat, lng);
    }
  }
  getAllCities() {
    this._getChemistDataService.getAllCitiesService().subscribe(
      response => {
        this.cities_array = response.data;
      });
  }// End of Get All Cities
  getCityAreas() {
   
      this._AddAreaNameService.getAreaName(this.City).subscribe(
        response => {

          this.areas_array = response.data;
        });
    

  }// End of Get Areas of City

  
  getChemists(id) {
    this.chemists_array = [];
    this._getChemistDataService.getChemistService().subscribe(
      response => {

        var abc = response.data.find(x => x.Chemist.Id == id);
        if (abc) {
          this.chemistData = abc;
          this.City = this.chemistData.Chemist.CityId;
          this.getCityAreas();
          $("#blah").attr("src",this.chemistData.ProfileURL)
          this.LogoUrl = this.chemistData.ProfileURL;
        }
        for (var i = 0; i < response.data.length; i++) {
          this.chemists_array.push(response.data[i].Chemist);
        }

      });

  


  }// End of Get Chemists


  resetAllErrors(){

    this.pharmacist_id_error = false;
    this.image_error  = false;
    this.full_name_id_error = false;
    this.address_id_error = false;
    this.PharmacyName_error = false;
    this.email_id_error = false;
    this.email_invalid_id_error = false;
    this.pharma_commission_id_error = false;
    this.city_id_error = false;
    this.area_id_error = false;
    this.latitude_id_error = false;
    this.longitude_id_error = false;
    
  }
  addChemist(){

    this.resetAllErrors();
    var error = false;



    if(this.PharmacistId == 0){
        this.pharmacist_id_error = true;
        error = true;
    }

    if (!ValidationModel.validateString(this.FullName)) {
      this.full_name_id_error = true;
      error = true;
    }

    if (this.LogoUrl == undefined) {
      this.image_error = true;
    }
    
    if (!ValidationModel.validateString(this.Address)) {
      this.address_id_error = true;
      error = true;

    }
    if (!ValidationModel.validateEmail(this.Email)) {
      $('#email_invalid_id_error').show();

    }
    if (!ValidationModel.validateString(this.Email)) {
      this.email_id_error = true;
      error = true;
    
    }
    if (this.chemistData.Chemist.PharmaCommission== 0) {
   
      this.pharma_commission_id_error  = true;
      error = true;
   
    }
    if (this.City == 0) {

      this.city_id_error =  true;
      error = true;

    }
    if (this.Area == 0) {
  
      this.area_id_error   = true;
      error = true;
   
    }
   
    if (this.Latitude == 0) {

      this.latitude_id_error = true;
      error = true;
 
    }
    if (this.Longitude == 0) {

      this.longitude_id_error = true;
      error = true;
 
    }


    if(error){

    }else{



      this.addLoader = true;
      this.actionBtn = false;


      this._addChemistServiceService.addChemist(this.Area, this.chemistData.Email, this.chemistData.Chemist.PharmaCommission, this.City, this.chemistData.Chemist.ContactPersonName, this.chemistData.Chemist.Address, this.Longitude, this.Latitude, this.PharmacistId, this.chemistData.Chemist.PharmacyName,this.LogoUrl).subscribe(
        response => {
          
          if(response.code == 200){

            this.addLoader = false;
            this.actionBtn = true;

            swal(response.message, "Logistic Created Successfully", "success");
      
            this.chemistData.Chemist.ContactPersonName = "";
            this.chemistData.Chemist.Address = "";
            this.chemistData.Email= "";
            this.chemistData.Chemist.PharmaCommission = 0;
            this.City = 0;
            this.Area = 0;
            this.chemistData.Chemist.PharmacyName = "";
            this.PharmacistId = 0;
            this.Latitude = 0;
            this.Longitude = 0;
            this.LogoUrl = undefined;
            $("#blah").attr("src","../../../../assets/images/pharma-avatar.png");

          }
         
        },err=>{

          this.addLoader = false;
          this.actionBtn = true;

          var obj = JSON.parse(err._body);
          swal(obj.message, "Logistic not created", "error");

        }


      ); 


    }
  }

}




